body{
    background-color: #252525;
    font-family: system-ui;
}
.Main-Div{
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 20px;
    gap:30px;
}
.Main-Head{
    color: white;
    text-align: center;
    font-size: 40px;
}
.Main-Head span{
    color: rgb(63 255 163);
    text-align: center;
}
.Main-Image img{
    width: 400px;
    border-radius: 15px;
    box-shadow: 4px 4px 6px #1b1b1b;
}
.search-box{
    display: flex;
}
input.search-field {
    padding: 10px 42px;
    background: #ffffff0f;
    color: white;
    border-radius: 20px 0px 0px 20px;
    font-size: 17px;
    border: none;
}
.generate-btn{
    padding: 10px 42px;
    background: #3fffa3;
    color: #252525;
    border-radius: 0px 20px 20px 0px;
    font-weight: 500;
    cursor: pointer;
}